*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.black-nav{
  width: 100%;
  height: 100px;
  line-height: 100px;
 
  background-color: #222;
}
.black-nav>h4{
  color: white;
  font-size: 40px;
  margin-left: 30px;
}
.list{
  width: 100%;
  height: 180px;
  border-bottom: 1px solid #222;
}
.list>h1{
  line-height: 80px;
  margin-left: 30px;
  display: inline-block;
  cursor: pointer;
}
.list>p{
  margin-left: 30px;
  font-size: 25px;
}
.list>button{
  width: 100px;
  height: 35px;
  margin: 20px 30px;
  background-color: aliceblue;
  border: none;
}
.list>span{
  font-size: 30px;
  cursor: pointer;
}

.modal{
  width: 40%;
  height: 400px;
  background-color: aliceblue;
  text-align: center;
  line-height: 100px;
  position: absolute;
  top:150px;
  left: 180px;
}